@charset "UTF-8";
@media (min-width: 400px) {
  .Header {
    /* topMenu의 ID를 가진 태그의 스타일 지정 */
    position: fixed;
    width: 95vw;
    z-index: 1; }
    .Header ul {
      display: inline-block;
      padding: 0;
      margin: 0; }
      .Header ul li {
        /* topMenu의 ID를 가진 태그 안에 <ul> 태그 안에 <li> 태그의 스타일을 지정 */
        list-style: none;
        /* <li> 태그는 위의 이미지처럼 목록을 나타내는 점을 없앤다 */
        color: white;
        /* 글씨 색을 흰색으로 설정 */
        background-color: #2d2d2d;
        /* 배경색을 진한 회색(RGB(2D,2D,2D)으로 설정 */
        float: left;
        /* <li>태그들이 왼쪽에 흐르게 설정(그러면 아래있는 내용은 오른쪽으로 옴) */
        line-height: 5vh;
        /* 글씨가 가운데로 오도록 설정하기 위해 한줄의 높이를 30px로 설정 */
        vertical-align: middle;
        /* 세로 정렬을 가운데로 설정(위의 line-height와 같이 설정 필요함) */
        text-align: center;
        /* 글씨 정렬을 가운데로 설정 */ }
    .Header .menuLink {
      /* topMenu 아이디를 가진 태그 안에 있는 menuLink 클래스 태그들의 스타일 설정 */
      text-decoration: none;
      /* 링크(<a> 태그)가 가지는 기본 꾸밈 효과(밑줄 등)을 없앰 */
      color: white;
      /* 폰트색을 흰색으로 설정 */
      display: block;
      /* 링크를 글씨가 있는 부분 뿐만아니라 전체 영역을 클릭해도 링크가 걸리게 설정 */
      width: 18vh;
      /* 메뉴링크의 넓이 설정 */
      font-size: 2.5vh;
      /* 폰트 사이즈 12px로 설정 */
      font-weight: bold;
      /* 폰트를 굵게 */
      font-family: "Trebuchet MS", Dotum, Arial;
      /* 기본 폰트 적용, 시스템 폰트를 종류별로 순서대로 */ }
      .Header .menuLink:hover {
        /* topMenu 아이디를 가진 태그 안에 menuLink클래스를 가진 태그에 마우스가 over되면 스타일 설정 */
        color: lightskyblue;
        /* 글씨 색을 붉은색으로 설정 */
        background-color: #4d4d4d;
        /* 배경색을 조금 더 밝은 회색으로 설정 */ }
  .nav-sm {
    display: none; }
  .nav-lg {
    display: block;
    width: 90vw;
    height: 7vh;
    margin-left: auto;
    margin-right: auto;
    text-align: right; }
    .nav-lg ul {
      display: inline-block;
      padding: 0;
      margin: 0; }
      .nav-lg ul li {
        /* topMenu의 ID를 가진 태그 안에 <ul> 태그 안에 <li> 태그의 스타일을 지정 */
        list-style: none;
        /* <li> 태그는 위의 이미지처럼 목록을 나타내는 점을 없앤다 */
        color: white;
        /* 글씨 색을 흰색으로 설정 */
        background-color: rgba(45, 45, 45, 0.7);
        float: left;
        /* <li>태그들이 왼쪽에 흐르게 설정(그러면 아래있는 내용은 오른쪽으로 옴) */
        line-height: 5vh;
        /* 글씨가 가운데로 오도록 설정하기 위해 한줄의 높이를 30px로 설정 */
        vertical-align: middle;
        /* 세로 정렬을 가운데로 설정(위의 line-height와 같이 설정 필요함) */
        text-align: center;
        /* 글씨 정렬을 가운데로 설정 */ }
        .nav-lg ul li:first-child, .nav-lg ul li:first-child > a {
          border-radius: 0% 0% 0% 15%/50%; }
        .nav-lg ul li:last-child, .nav-lg ul li:last-child > a {
          border-radius: 0% 0% 15%/50% 0%; }
  .nav-lg a {
    /* menu-link 클래스 태그들의 스타일 설정 */
    text-decoration: none;
    /* 링크(<a> 태그)가 가지는 기본 꾸밈 효과(밑줄 등)을 없앰 */
    color: lightgrey;
    /* 폰트색을 흰색으로 설정 */
    display: block;
    /* 링크를 글씨가 있는 부분 뿐만아니라 전체 영역을 클릭해도 링크가 걸리게 설정 */
    width: 10vw;
    /* 메뉴링크의 넓이 설정 */
    font-size: 2.5vh;
    /* 폰트 사이즈 12px로 설정 */
    font-weight: bold;
    /* 폰트를 굵게 */ }
    .nav-lg a:hover, .nav-lg a .active {
      background: #1ABC9C; }
  .nav-sm:active {
    background: #1ABC9C; } }

@media (max-width: 400px) {
  .Header {
    display: none; } }
